  width: 18px;
}

.form-item.-radio-button label:after, .form-item.-checkbox label:after {
  transition-property: border, top, transform;
  transition-duration: 0.5s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
