  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  zoom: 1;
  /* For IE 6/7 (trigger hasLayout) */
  height: 60px;
  position: absolute;
  right: 0;
  top: 10px;
  width: 100%;
  width: auto;
  z-index: 5;
}

#navigation:before, #navigation:after {
  content: '';
  display: table;
}

#navigation:after {
  clear: both;
}

