.table-wrap {
  margin-bottom: 20px;
  overflow: hidden;
  padding: 6px 0;
  position: relative;
  width: 100%;
}

.table-wrap ::-webkit-scrollbar {
  height: 8px;
  width: 4px;
}

.table-wrap ::-webkit-scrollbar-thumb {
  background: #9e9e9e;
}

.table-wrap:before, .table-wrap:after {
  transition-property: opacity;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  content: '';
  height: 100%;
  opacity: 0;
  position: absolute;
  top: 0;
  width: 50px;
}

.table-wrap:before {
  border-radius: 0 10px 10px 0 / 0 50% 50% 0;
  box-shadow: 4px 0 10px black;
  right: 100%;
}

.table-wrap:after {
  border-radius: 10px 0 0 10px / 50% 0 0 50%;
  box-shadow: -4px 0 10px black;
  left: 100%;
}

.table-wrap.left, .table-wrap.right {
  transition-property: border;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
}

.table-wrap.left {
  border-left: 1px solid #f4f4f4;
}

.table-wrap.left:before {
  opacity: 0.25;
}

.table-wrap.right {
  border-right: 1px solid #f4f4f4;
}

.table-wrap.right:after {
  opacity: 0.25;
}

.table-wrap.-clean:before, .table-wrap.-clean:after {
  display: none;
}

.table-wrap.-clean.right {
  border-right: 0;
  padding: 0;
}

.table-wrap.-clean .table-responsive {
  border: 0;
  overflow: visible;
}

.table-responsive {
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  border: 1px solid #dadada;
  margin-bottom: 15px;
  overflow-y: hidden;
  overflow-x: scroll;
  width: 100%;
}

.table-responsive table {
  margin-bottom: 0;
}

.btn-print-table {
  transition-property: position;
  transition-duration: 0.5s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  font-size: 1.6em;
  max-width: 200px;
  padding: 10px;
  width: 100%;
}

@media screen and (min-width: 980px), print {
  .btn-print-table {
    display: none;
  }
}
